import React from 'react';
import cssImage from '../t/experience/Logo.png';

function TwoColumnLayout() {
  return (
    <div name="about" className="w-full h-auto flex flex-col p-8 bg-white dark:bg-white text-black dark:text-black ">
      <div className="max-w-screen-lg mx-auto p-4 ">
        <div style={{ display: 'flex', flexWrap: 'wrap' }}>
          <div style={{ flex: '0 0 30%', padding: '10px' }}>
            <img
              src={cssImage} // Use the variable name without quotes
              alt="Your Image"
              style={{ width: '500', height: '1147' }}
            />
          </div>
          <div style={{ flex: '0 0 70%', padding: '10px', }}>
            <h1 style={{ marginTop: '30px', fontSize: '15px' }}>Mechanics and Materials Laboratory</h1>
          </div>
        </div>
      </div>
    </div>
  );
}

export default TwoColumnLayout;
